div.cke_float[id*="cke_keditor-ui-"] {
  padding: 0;
  margin-top: -1px;
}
@media screen and (min-width: 1024px) {
  div.cke_float[id*="cke_keditor-ui-"] {
    margin-left: -1px;
  }
}
div.cke_float[id*="cke_keditor-ui-"] .cke_top {
  padding: 3px;
}
@media screen and (min-width: 1024px) {
  div.cke_float[id*="cke_keditor-ui-"] .cke_top {
    padding: 5px 1px 0 6px;
  }
}
div.cke_float[id*="cke_keditor-ui-"] .cke_toolbox {
  display: block !important;
  overflow-x: auto;
  overflow-y: hidden;
}
div.cke_float[id*="cke_keditor-ui-"] .cke_toolbox::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
div.cke_float[id*="cke_keditor-ui-"] .cke_toolbox::-webkit-scrollbar-track {
  background: #eee;
  -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.1);
}
div.cke_float[id*="cke_keditor-ui-"] .cke_toolbox::-webkit-scrollbar-thumb {
  background: #ddd;
  -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.2);
}
div.cke_float[id*="cke_keditor-ui-"] .cke_toolbox::-webkit-scrollbar-thumb:hover {
  background: #cdcdcd;
}
div.cke_float[id*="cke_keditor-ui-"] .cke_toolbox::-webkit-scrollbar-thumb:active {
  background: #cdcdcd;
}
div.cke_float[id*="cke_keditor-ui-"] .cke_toolbar {
  float: none !important;
  display: inline-block !important;
  vertical-align: top !important;
}
div.cke_float[id*="cke_keditor-ui-"] .cke_toolbar_break {
  display: none !important;
}
.cke_screen_reader_only {
  top: 0;
  left: 0;
  z-index: -1;
}
